.Cell .am-card {
  .am-card-header {
    font-size: 0.14rem;
    color: #888;
    margin: 0.15rem 0.15rem 0.08rem;
  }
  .am-card-item {
    display:block;
    background-color: #fff;
    border-radius: 0.02rem;
    margin: 0.06rem 0.1rem;
  }
  .am-card-minfo {
    min-height: 0.86rem;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    padding: 0 0.16rem;
    box-sizing: border-box;
    .am-card-thumb {
      margin-right: 0.1rem;
      img {
        width: 1.5rem;
        height: 1.5rem;
        float: left;
        border-radius: 2px;
        -webkit-background-clip: padding-box;
      }
    }
    .am-card-content {
      -webkit-box-flex: 1;
      -webkit-flex: 1;
      min-width: 1rem;
      .am-card-title {
        font-size: 0.2rem;
        line-height: 0.25rem;
        color: #333;
      }
      .am-card-subtitle {
        font-size: 0.14rem;
        line-height: 0.2rem;
        color: #999;
        margin-bottom: 0.02rem;
      }
    }
  }
  .am-card-footnote {
    background: linear-gradient(180deg, #fff, #fff) top left no-repeat,
      linear-gradient(0deg, #fff, #fff) bottom left no-repeat,
      linear-gradient(180deg, #eee, #eee 33%, transparent 33%) top left no-repeat,
      linear-gradient(0deg, #ddd, #ddd 33%, transparent 33%) bottom left no-repeat, #fff;
    background-size: 0 0, 100% 1px, 100% 1px, 0 0;
    padding: 0.1rem 0;
    margin: -0.04rem 0.16rem 0;
    font-size: 0.14rem;
    line-height: 0.2rem;
    color: #999;
    img {
      display: inline-block;
      width: 0.14rem;
      height: 0.14rem;
      margin-right: 0.04rem;
      vertical-align: -0.02rem;
    }
  }
}

.am-card .am-card-noicon.am-card-minfo,
.am-card.am-card-noicon .am-card-minfo {
  min-height: 0.81rem;
  padding: 0.16rem;
}
.am-card .am-card-noicon .am-card-title,
.am-card.am-card-noicon .am-card-title {
  font-size: 0.18rem;
  margin-bottom: 0.04rem;
}
.am-card .am-card-noicon .am-card-subtitle,
.am-card.am-card-noicon .am-card-subtitle {
  margin: 0;
}

.am-card a.am-card-minfo .hover,
.am-card a.am-card-minfo:active {
  background: rgba(0, 0, 0, 0.03);
}
