.Detail .am-article {
  font-size: 0.15rem;
  line-height: 0.24rem;
  padding: 0.2rem 0.16rem;
  color: #333;
  h2 {
    font-size: 0.24rem;
    font-weight: 500;
    color: #333;
    line-height: 0.325rem;
  }
  h4 {
    font-size: 0.17rem;
    font-weight: 500;
    line-height: 0.42rem;
    color: #333;
  }
  time {
    display: block;
    margin: 0.16rem 0 0.15rem;
    font-size: 0.15rem;
    color: #999;
  }
  .am-article-source {
    font-size: 0.15rem;
    color: #108ee9;
  }
  dl {
    margin: 0 0 0.2rem;
    dt {
      font-size: 0.14rem;
      font-weight: 900;
      color: #000;
      margin-bottom: 0.03rem;
    }
    dd {
      color: #000;
      line-height: 0.2rem;
    }
  }
  p {
    padding-bottom: 0.17rem;
    font-size: 0.17rem;
    font-weight: 300;
    line-height: 0.27rem;
  }
  blockquote {
    margin-bottom: 16px;
    padding-left: 16px;
    border-left: 4px solid #ddd;
    color: #999;
  }
  .am-article-tags .am-tag-item {
    display: inline-block;
    margin-right: 6px;
    padding: 0.04rem 0.12rem;
    font-size: 0.14rem;
    background: #f7f7f7;
    color: #1e1e1e;
  }
  img {
    max-width: 100%;
    vertical-align: bottom;
  }
  .am-article-footer {
    margin-top: -0.08rem;
  }
}
